import React, { Component, useEffect } from "react";
import Draggable from 'react-draggable';
import { OverlayTrigger, Tooltip } from "react-bootstrap";
import { checkPermission } from "../../../../../../actions/collaboratorActions";

const Hotspot = (props) => {

  const { ImageClass, item, enableHotspot, width, height, spotWidth, spotHeight, onControlledDrag, changePrdId, ctaData } = props
  useEffect(() => {
    return
  }, [item.postproduct])

  return (<div className={`h-100 w-100 ${ImageClass}`}>
    {enableHotspot == 1 && item.postproduct && item.postproduct.length > 0 && spotWidth ?
      item.postproduct.map((Item, index) => {
        let left = (spotWidth * Item.hotspot.left.replace("%", "")) / 100;
        let top = (spotHeight * Item.hotspot.top.replace("%", "")) / 100;
        return <Draggable bounds="parent" onStop={onControlledDrag} key={`hotspot_${Item.ugc_product}`} onStart={() => changePrdId(Item.ugc_product)} defaultPosition={{ x: left, y: top }} >
          <div className={`h-16px w-16px position-absolute`}>
              <OverlayTrigger placement='top' overlay={<Tooltip data-theme="light">
                <div className="text-start">
                  {Item.product && Item.product.product_title ? Item.product.product_title : ''}
                  {((Item.product.product_discount && Item.product.product_discount > 0) || (Item.product.product_price && Item.product.product_price > 0)) ?
                    <div className="fw-bold fs-6">{Item?.product?.price_currency_symbol}{(Item.product.product_price == Item.product.product_discount ? Item.product.product_price : Item.product.product_discount > 0 ? Item.product.product_discount : Item.product.product_price)}</div> : ''}
                </div>
              </Tooltip>}>

              <div className={`blob ${ctaData?.hotspot_type == 1 ? 'blob_002' : 'blob_001'}`} style={{ backgroundColor: ctaData?.hotspot_color }}>
                <div className={`f-center fw-medium fs-8`} style={{ color: ctaData?.hotspot_type_color, lineHeight: '20px' }}>
                  {ctaData?.hotspot_type == 1 ? index + 1 : ''}
                </div>
              </div>

              </OverlayTrigger>
            </div>
        </Draggable>
      }) : ''}
  </div>
  );
}

export default Hotspot